@keyframes rotater {
    0% {
        /* IE 9 */
        -webkit-transform: rotate(0deg);
        /* Chrome, Safari, Opera */
        transform: rotate(0deg);
    }
    20% {
        /* IE 9 */
        -webkit-transform: rotate(5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(5deg);
    }
    40% {
        /* IE 9 */
        -webkit-transform: rotate(-5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(-5deg);
    }
    60% {
        /* IE 9 */
        -webkit-transform: rotate(5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(5deg);
    }
    80% {
        /* IE 9 */
        -webkit-transform: rotate(-5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(-5deg);
    }
    100% {
        /* IE 9 */
        -webkit-transform: rotate(0deg);
        /* Chrome, Safari, Opera */
        transform: rotate(0deg);
    }
}
@-webkit-keyframes rotater {
    0% {
        /* IE 9 */
        -webkit-transform: rotate(0deg);
        /* Chrome, Safari, Opera */
        transform: rotate(0deg);
    }
    20% {
        /* IE 9 */
        -webkit-transform: rotate(5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(5deg);
    }
    40% {
        /* IE 9 */
        -webkit-transform: rotate(-5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(-5deg);
    }
    60% {
        /* IE 9 */
        -webkit-transform: rotate(5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(5deg);
    }
    80% {
        /* IE 9 */
        -webkit-transform: rotate(-5deg);
        /* Chrome, Safari, Opera */
        transform: rotate(-5deg);
    }
    100% {
        /* IE 9 */
        -webkit-transform: rotate(0deg);
        /* Chrome, Safari, Opera */
        transform: rotate(0deg);
    }
}
