/*  popup    */
.super_nav_1 {
    width: 490px;
    position: absolute;
    display: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.super_nav_1 .super_seting {
    width: 100%;
    background: #fff;
    margin-bottom: 2px;
    background: #ffffff;
    background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(47%, #f6f6f6), color-stop(100%, #ededed));
    background: -webkit-linear-gradient(top, #ffffff 0%, #f6f6f6 47%, #ededed 100%);
    background: -ms-linear-gradient(top, #ffffff 0%, #f6f6f6 47%, #ededed 100%);
    background-repeat: no-repeat;
}
.super_nav_1 .super_seting ul {
    text-align: center;
}
.super_nav_1 .super_seting ul li {
    display: inline-block;
    font-weight: bold;
    width: 32%;
}
.super_nav_1 .super_seting ul li a {
    padding: 4px;
    display: block;
    width: 125px;
    background-color: #F4F4F4;
    margin: 7px auto;
    color: #5A5A5A;
    font-size: 12px;
    text-decoration: none;
    cursor: pointer;
}
.super_nav_1 .super_seting ul li a.btn-green {
    color: #fff;
    font-family: 'CenturyGothic-Bold';
    text-transform: uppercase;
    font-size: 16px;
    width: 100%;
}
.super_nav_1 .item-wrap {
    -moz-column-count: 2;
    /*  Firefox */
    -webkit-column-count: 2;
    /*  Safari  Chrome */
    column-count: 2;
    -moz-column-gap: 2px;
    -webkit-column-gap: 2px;
    column-gap: 2px;
}
.super_nav_1 .super_item {
    position: relative;
    width: 100%;
    margin: 1px 0 0 0 ;
    cursor: pointer;
    height: 38px;
    display: inline-block;
    margin-bottom: 2px;
    background: #ffffff;
    background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(47%, #f6f6f6), color-stop(100%, #ededed));
    background: -webkit-linear-gradient(top, #ffffff 0%, #f6f6f6 47%, #ededed 100%);
    background: -ms-linear-gradient(top, #ffffff 0%, #f6f6f6 47%, #ededed 100%);
    background-repeat: no-repeat;
    box-shadow: 0 0 1em rgba(0,0,0,0.7);
    border: 1px solid #ccc;
}
.super_nav_1 .super_item:hover {
    background: rgba(80, 166, 175, 0.56);
}
.super_nav_1 .super_item:hover label {
    color: #fff;
}
.super_nav_1 input[type="checkbox"] {
    position: absolute;
    z-index: -1;
    visibility: hidden;
    top: 3px;
}
.super_nav_1 label {
    width: 100%;
    cursor: pointer;
    padding: 14px 12px 12px 35px;
    font-size: 12px;
    height: 28px;
    display: inline-block;
    color: #5A5A5A;
    font-family: 'CenturyGothic';
    text-align: left;
}
.super_nav_1 label:before {
    content: "";
    position: absolute;
    left: 10px;
    top: 12px;
    background: url(../image/sprite.png) no-repeat -63px -458px;
    width: 15px;
    height: 14px;
}
.super_nav_1 input:checked + label:before {
    background: url(../image/sprite.png) no-repeat -88px -458px;
    width: 15px;
    height: 14px;
}
