.profile-watches-container {
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
.profile-watches-container article, .profile-watches-container aside, .profile-watches-container details, .profile-watches-container figcaption, .profile-watches-container figure, 
.profile-watches-container footer, .profile-watches-container header, .profile-watches-container hgroup, .profile-watches-container menu, .profile-watches-container nav, .profile-watches-container section {
    display: block;
}

.profile-watches-container {
    line-height: 1;
    font: 100%/1.5em "PT Sans", Times, sans-serif;
    background-color: #f0f0f0;
}

.profile-watches-container ol, .profile-watches-container ul {
    list-style: none;
}

.profile-watches-container a, .profile-watches-container a:hover{
    text-decoration: none;
}

.profile-watches-container blockquote, .profile-watches-container q {
    quotes: none;
}

.profile-watches-container .clearfix:before,
.profile-watches-container .clearfix:after {
    content: " ";
    display: table;
}

.profile-watches-container .clearfix:after {
    clear: both;
}

.profile-watches-container .left {
    float: left;
}

.profile-watches-container .right {
    float: right;
}

.profile-watches-container blockquote:before, .profile-watches-container blockquote:after,
.profile-watches-container q:before, .profile-watches-container q:after {
    content: '';
    content: none;
}

.profile-watches-container table {
    border-collapse: collapse;
    border-spacing: 0;
}

@font-face {
    font-family: 'Century Gothic'; /*   */
    src: url(../fonts/CenturyGothic.ttf); /*      */
}

.profile-watches-container .observations-product {
    min-height: 263px;
    max-width: 485px;
    margin: 0 auto;
    display: inline-block;
    padding: 5px;
    text-align: center;
    -webkit-transition: box-shadow 0.3s !important;
    transition: box-shadow 0.3s !important;
}

.profile-watches-container .observations-product:hover {
    box-shadow: 0px 0px 25px -8px rgba(0,0,0,0.75);
}

.profile-watches-container .product-img {
    max-width: 200px;
    padding: 3px;
    padding-bottom: 0;
}

.profile-watches-container .girls {
    width: 140px;
    height: 235px;
}

/*    checkbox */
.profile-watches-container input[type="checkbox"] {
    display: none;
}

.profile-watches-container input[type="checkbox"] + label {
    display: block;
    text-align: left;
}

/*        */
.profile-watches-container input[type="checkbox"] + label span {
    position: absolute;
    /*display: inline-block;*/
    display: none;
    vertical-align: middle;
    width: 16px;
    height: 16px;
    margin: 5px 0 0 5px;
    background: url(../image/profile_imgs/checkbox-sprite.png) no-repeat  0 0;
    cursor: pointer;
}

.profile-watches-container input[type="checkbox"]:checked + label span {
    background:url(../image/profile_imgs/checkbox-sprite.png) no-repeat -25px 0;
}

.profile-watches-container .observations-close-btn {
    margin-top: 8px;
    float: right;
    width: 14px;
    height: 14px;
    cursor: pointer;
    background: url(../image/profile_imgs/close-btn-sprite.png) no-repeat 0 0;
}

.profile-watches-container .observations-close-btn:hover {
    background: url(../image/profile_imgs/close-btn-sprite.png) no-repeat  0 -26px;
}

.profile-watches-container .brand-info {
    text-align: left;
    max-width: 300px !important;
    padding: 8px 0 0 15px;
    display: inline-block;
}

.profile-watches-container .info-about-product {
    width: 100% !important;
}

.profile-watches-container .info-about-product label {
    width: 90px;
    text-align: left;
    text-transform: uppercase;
    font-size: 11px;
    font-weight: bold;
    float: left;
    color: #666666;
    font-family: 'Century Gothic';
}

.profile-watches-container .info-about-product p {
    width: 180px;
    text-align: left;
    font-size: 11px;
    font-family: 'Century Gothic';
}

.profile-watches-container .info-about-product a {
    font-size:  11px;
    font-weight: bold;
    color: #53a3b3;
    font-family: 'Century Gothic';
}

.profile-watches-container .review {
    margin-left: 8px;
    color: #383333 !important;
    text-decoration: underline;
    position: absolute;
}

.profile-watches-container .price-block {
    width: 135px;
    text-align: center;
    padding: 7px;
    background-color: #f9f9f9;
    font-size: 14px;
}

.profile-watches-container .old-price {
    float: left;
    margin: 27px 0 0 25px;
    font-family: 'Century Gothic';
}

.profile-watches-container .old-price>p {
    padding-bottom: 8px;
    font-size: 15px;
    font-weight: bold;
}

.profile-watches-container .new-price>p {
    padding-bottom: 8px;
    font-size: 15px;
    font-weight: bold ;
}

.profile-watches-container .new-price {
    float: left;
    margin: 27px 0 0 25px;
    opacity:  0.5;
    font-family: 'Century Gothic';
}

.profile-watches-container .bold-text {
    font-weight: bold;
}

.profile-watches-container .pink {
    color: #b62371;
}

.profile-watches-container .fast-order {
    width: 135px;
    padding: 7px;
    background-color: #959595;
    color: #fff;
    cursor: pointer;
    font: bold 1em "Century Gothic",sans-serif;
    text-transform: uppercase;
    box-shadow: inset 0px 0px 3px rgba(0,0,0,0.5);
}

.profile-watches-container .add-basket {
    width: 135px;
    padding: 7px;
    background-color: #56a5b1;
    color: #fff;
    cursor: pointer;
    font: bold 1em "Century Gothic", sans-serif;
    text-transform: uppercase; 
    box-shadow: inset 0px 0px 3px rgba(0,0,0,0.5);
}

.profile-watches-container .selection {
    width: 100%;
    padding: 15px 40px;
    /*display: inline-block;*/
    display: none;
}

.profile-watches-container .selection-buttons {
    float: right;
}

.profile-watches-container .selection-links {
    float: right;
    font-size: 14px;
    margin-right: 30px;
    margin-top: 6px;
}

.profile-watches-container .selection-links a {
    font-family: 'Century Gothic';
    text-decoration: underline;
    color: #666666;
    margin-right: 10px !important; 
}

.profile-watches-container .basket-btn, .profile-watches-container .delete-btn {
    font-family: 'Century Gothic';
    padding: 5px 20px;
    background-color: #4f9fab;
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: bold;
    display: inline-block;
    cursor: pointer; 
    box-shadow: inset 0px 0px 3px rgba(0,0,0,0.5);
}

.profile-watches-container ul {
    list-style: none;
}

.profile-watches-container a {
    text-decoration: none;
}

.profile-watches-container .clearfix:after {
    content: '';
    clear: both;
    display: table;
}
.profile-watches-container {
    max-width: 100%;
    margin: 0 auto;
    min-height: 500px;
    text-align: center;
}
.profile-watches-container .content_block article {
    width:420px;
    display: inline-block;
    vertical-align: top;
    margin: 10px;
    text-align: center;
}

.profile-watches-container .white-color {
    border-right: 1px solid #cccccc !important;
    background: rgba(255,255,255,1) !important;
}

.profile-watches-container .bg-color {
    background-color: #f5f5f5;
}

.profile-watches-container .border {
    border: 1px solid #cccccc;
}

.profile-watches-container .active-1 {
    display: block;
}

.profile-watches-container .disable {
    display: none;
}

.profile-watches-container .opacity-0 {
    opacity: 0;
}

.profile-watches-container .opacity-1 {
    opacity: 1;
}

.profile-watches-container .opacity-2 {
    opacity: 0.2;
}

