@media screen and (max-width: 991px) {
    .main-container .basket-conteiner .basket-form {
        width: 70%;
    }
    #final-basket-conteiner .table-final-result {
        width: 65%;
    }
    .main-container .basket-conteiner .basket-table .basket-img {
        max-height: 100px;
    }
}
@media screen and (max-width: 767px) {  
    .main-container .basket-conteiner .basket-form {
        width: 85%;
    }
    .main-container .basket-conteiner .submit-show, .main-container .basket-conteiner .submit-clear-all {
        width: 32%;
        font-size: 13px;
    }
    .main-container .basket-conteiner .basket-empty-conteiner .basket-empty-content {
        width: 70%;
    }
    .main-container .basket-conteiner .table-res {
        border: 0;
    }
    .main-container .basket-conteiner #final-basket-conteiner .table-final-result {
        width: 75%;
    }
}
@media screen and (max-width: 600px) {
    .main-container .basket-conteiner .basket-form {
        width: 95%;
    }
    .main-container .basket-conteiner .basket-form .btn-submit-order {
        font-size: 18px;
    }
    .main-container .basket-conteiner .basket-empty-conteiner .basket-empty-content {
        width: 80%;
    }
    .main-container .basket-conteiner #final-basket-conteiner .table-final-result {
        width: 85%;
    }
}
@media screen and (max-width: 480px) {
    .main-container .basket-conteiner .submit-show, .main-container .basket-conteiner .submit-clear-all {
        width: 45%;
        margin: 15px 5px;
        margin: 15px 5px;
        font-size: 15px;
    }
    .main-container .basket-conteiner .basket-empty-conteiner .basket-empty-content {
        width: 85%;
    }
    .main-container .basket-conteiner #final-basket-conteiner .table-final-result {
        width: 95%;
    }
}
@media screen and (max-width: 400px) {
    .main-container .basket-conteiner .basket-form {
        width: 100%;
    }
    .main-container .basket-conteiner .basket-form .btn-submit-order {
        width: 70%;
        font-size: 16px;
    }
    .main-container .basket-conteiner .submit-show, .main-container .basket-conteiner .submit-clear-all {
        margin: 15px 0px;
        font-size: 12px;
    }
}
