/*profile all rulse*/
#profile-conteiner-full {
    background-color: #f0f0f0 !important;
    padding: 40px 0;
}
#profile-conteiner-full * {
    font-family: "CenturyGothic";
}
#profile-conteiner-full .content {
    width: 100%;
    /*min-height: 143px;*/
    background: #fff;
    padding: 3px;
    box-shadow: 0px 0px 25px -8px rgba(0,0,0,0.75);
}
#profile-conteiner-full .content_block {
    width: 100%;
}
/*left menu*/
#profile-conteiner-full .left-menu {
    padding-right: 0;
}
#profile-conteiner-full .left-menu button {
    display: none;
    margin: 10px;
}
#profile-conteiner-full .left-menu button span {
    font-family: 'Glyphicons Halflings';
    color: #51a3af;
}
#profile-conteiner-full .left-menu ul li {
    position: relative;
    width: 96%;
}
#profile-conteiner-full .left-menu ul li a {
    font-size: 13px;
    line-height: 32px;
    font-weight: 300;
    display: block;
    text-align: center;
    background: #FFF;
    margin-bottom: 5px;
    border: 1px solid #dbdbdb;
    color: #6FA1CC;
}
#profile-conteiner-full .left-menu ul li a:hover {
    color: #fff;
    background: #60D5E6;
}
#profile-conteiner-full .left-menu ul li a:focus,
#profile-conteiner-full .left-menu ul li a:active,
#profile-conteiner-full .left-menu ul li a:visited {
    outline: none;
    text-decoration: none;
}
#profile-conteiner-full .left-menu ul li a.active {
    background: #51a3af;
    color: #fff;
}
#profile-conteiner-full .left-menu ul li span {
    position: absolute;
    background: #88becb;
    color: #fff;
    font-size: 10px;
    line-height: 20px;
    font-weight: 700;
    border-radius: 50px;
    padding: 0 7px;
    right: -5px;
    top: -3px;
    cursor: default;
}
.profile-container-all .form-control {
    border-radius: 0;
}
/*profile-main-ruls*/
.profile-container .content {
    padding: 10px;
    margin-bottom: 30px;
}
.profile-container .row {
    margin-left: 0;
}
.profile-container .namehead {
    border-bottom: 1px solid #e8e8e8;
    padding: 2px 0;
    font-size: 16px;
    line-height: 25px;
}
.profile-container .namehead span {
    color: #999999;
    margin-left: 10px;
}
.profile-container .row .row-block {
    margin-top: 10px;
    margin-bottom: 10px;
}
.profile-container .profile-title {
    display: block;
    width: 100%;
    color: #666666;
    font-size: 14px;
    font-weight: bold;
    line-height: 18px;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 5px;
    padding-left: 30px;
}
.profile-container .custom-border {
    padding: 3px;
}
.profile-container .avatar {
    border: 1px solid #cccccc;
    padding: 5px;
    width: 140px;
    min-height: 75px;
    float: left;
}
.profile-container .avatar img {
    width: 100%;
    height: 100%;
}
.profile-container .left-input-block {
    text-transform: uppercase;
    width: 350px;
}
.profile-container .input_block {
    margin: 7px 5px !important;
}
.profile-container .input_block label,
.profile-container .input_block input {
    margin: 0 2px 0 0;
    font-size: 13px;
    color: #666666;
    width: 63px;
}
.profile-container .input_block input {
    border: 1px solid #cccccc;
    padding: 3px;
    width: calc(100% - 70px) !important;
}
.profile-container .slick-slider .slick-prev,
.profile-container .slick-slider .slick-next {
    opacity: 0;
    -webkit-transition: opacity 0.5s;
